*{
    box-sizing: border-box;
}
.d-flex {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
}

.color-box {
    width: 100%;
    height: 200px;
    background: red;
    border-radius: 5px;
    box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.25);
}

form {
    margin-top: 15px;
}

form input {
    width: 100%;
    border: 2px solid #eee;
    border-radius: 3px;
    padding: 10px;
    margin-bottom: 10px;
    outline: none;
}
.btn{
    cursor: pointer;
}